import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';

const Header = () => {
  const navigate = useNavigate();
  const [activeLanguage, setActiveLanguage] = useState('RU');

  const handleLogoClick = () => {
    navigate('/');
    // Дополнительно скроллим к hero блоку после небольшой задержки
    setTimeout(() => {
      const heroElement = document.getElementById('hero');
      if (heroElement) {
        heroElement.scrollIntoView({ behavior: 'smooth', block: 'start' });
      }
    }, 100);
  };

  const handleLanguageChange = (lang) => {
    setActiveLanguage(lang);
    // Здесь можно добавить логику смены языка
    console.log('Язык изменен на:', lang);
  };


  return (
    <header className="bg-white shadow-md sticky top-0 z-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center py-4">
          {/* Логотип */}
          <button
            onClick={handleLogoClick}
            className="w-14 h-14 flex items-center justify-center hover:opacity-90 transition-opacity focus:outline-none rounded-lg"
            aria-label="Перейти на главную страницу"
          >
            <img
              src={`/logo.png?v=${Date.now()}`}
              alt="Логотип НЦОЗ МЗ РК"
              className="w-14 h-14 object-contain"
              style={{
                backgroundColor: 'transparent',
                filter: 'none',
                mixBlendMode: 'multiply'
              }}
              onError={(e) => {
                e.target.style.display = 'none';
                e.target.nextSibling.style.display = 'block';
              }}
            />
            <div
              className="w-14 h-14 flex items-center justify-center text-blue-600 font-bold text-base"
              style={{ display: 'none' }}
            >
              НЦОЗ
            </div>
          </button>

          {/* Навигация */}
          <nav className="hidden md:flex items-center space-x-8">
            <Link
              to="/about"
              className="text-gray-700 hover:text-blue-600 transition-colors duration-200 font-medium"
            >
              О нас
            </Link>
            <Link
              to="/news"
              className="text-gray-700 hover:text-blue-600 transition-colors duration-200 font-medium"
            >
              Новости
            </Link>
            <Link
              to="/publications"
              className="text-gray-700 hover:text-blue-600 transition-colors duration-200 font-medium"
            >
              Публикации
            </Link>
            <Link
              to="/guidelines"
              className="text-gray-700 hover:text-blue-600 transition-colors duration-200 font-medium"
            >
              Рекомендации
            </Link>
            <Link
              to="/media"
              className="text-gray-700 hover:text-blue-600 transition-colors duration-200 font-medium"
            >
              Медиа
            </Link>
            <Link
              to="/pdf-archive"
              className="text-gray-700 hover:text-blue-600 transition-colors duration-200 font-medium"
            >
              Архив
            </Link>
            <Link
              to="/partners"
              className="text-gray-700 hover:text-blue-600 transition-colors duration-200 font-medium"
            >
              Партнеры
            </Link>
            <Link
              to="/contacts"
              className="text-gray-700 hover:text-blue-600 transition-colors duration-200 font-medium"
            >
              Контакты
            </Link>
          </nav>

          {/* Языковой переключатель */}
          <div className="flex items-center space-x-4">
            {/* Языковой переключатель */}
            <div className="flex items-center space-x-2">
              <button
                onClick={() => handleLanguageChange('RU')}
                className={`px-3 py-2 text-sm rounded-md transition-colors font-medium shadow-sm ${
                  activeLanguage === 'RU'
                    ? 'bg-blue-600 text-white hover:bg-blue-700'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200 hover:text-gray-900'
                }`}
              >
                RU
              </button>
              <button
                onClick={() => handleLanguageChange('EN')}
                className={`px-3 py-2 text-sm rounded-md transition-colors font-medium shadow-sm ${
                  activeLanguage === 'EN'
                    ? 'bg-blue-600 text-white hover:bg-blue-700'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200 hover:text-gray-900'
                }`}
              >
                EN
              </button>
              <button
                onClick={() => handleLanguageChange('KZ')}
                className={`px-3 py-2 text-sm rounded-md transition-colors font-medium shadow-sm ${
                  activeLanguage === 'KZ'
                    ? 'bg-blue-600 text-white hover:bg-blue-700'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200 hover:text-gray-900'
                }`}
              >
                KZ
              </button>
            </div>
          </div>

          {/* Мобильное меню */}
          <div className="md:hidden">
            <button className="p-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-md transition-colors">
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
              </svg>
            </button>
          </div>
        </div>
      </div>
    </header>
  );
};

export default Header;
