import React, { useState, useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';

const Sidebar = () => {
  const [activeSection, setActiveSection] = useState('');
  const [isVisible, setIsVisible] = useState(false);
  const location = useLocation();

  // Показывать сайдбар только на главной странице
  const isHomePage = location.pathname === '/';

  const getNavigationIcon = (id) => {
    switch (id) {
      case 'hero':
        return (
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 10V3L4 14h7v7l9-11h-7z" />
          </svg>
        );
      case 'program':
        return (
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5H7a2 2 0 00-2 2v10a2 2 0 002 2h8a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2m-6 9l2 2 4-4" />
          </svg>
        );
      case 'registration':
        return (
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
          </svg>
        );
      case 'location':
        return (
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
          </svg>
        );
      default:
        return (
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6V4m0 2a2 2 0 100 4m0-4a2 2 0 110 4m-6 8a2 2 0 100-4m0 4a2 2 0 100 4m0-4v2m0-6V4m6 6v10m6-2a2 2 0 100-4m0 4a2 2 0 100 4m0-4v2m0-6V4" />
          </svg>
        );
    }
  };

  // Элементы навигации для главной страницы
  const homeNavigationItems = [
    { id: 'hero', label: 'Главная' },
    { id: 'program', label: 'Программа' },
    { id: 'registration', label: 'Регистрация' },
    { id: 'location', label: 'Место' }
  ];

  // Элементы навигации для других страниц
  const pageNavigationItems = [
    { id: 'about', label: 'О нас', path: '/about' },
    { id: 'news', label: 'Новости', path: '/news' },
    { id: 'publications', label: 'Публикации', path: '/publications' },
    { id: 'guidelines', label: 'Рекомендации', path: '/guidelines' },
    { id: 'media', label: 'Медиа', path: '/media' },
    { id: 'pdf-archive', label: 'Архив', path: '/pdf-archive' },
    { id: 'partners', label: 'Партнеры', path: '/partners' },
    { id: 'contacts', label: 'Контакты', path: '/contacts' }
  ];

  const navigationItems = isHomePage ? homeNavigationItems : pageNavigationItems;

  useEffect(() => {
    if (!isHomePage) return;

    const handleScroll = () => {
      const scrollPosition = window.scrollY;
      const windowHeight = window.innerHeight;

      // Показывать сайдбар после прокрутки 300px
      setIsVisible(scrollPosition > 300);

      // Определение активной секции для главной страницы
      const sections = homeNavigationItems.map(item => item.id);
      let current = '';

      sections.forEach(sectionId => {
        const element = document.getElementById(sectionId);
        if (element) {
          const rect = element.getBoundingClientRect();
          if (rect.top <= windowHeight * 0.3 && rect.bottom >= windowHeight * 0.3) {
            current = sectionId;
          }
        }
      });

      setActiveSection(current);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [isHomePage]);

  const handleNavigation = (item) => {
    if (isHomePage) {
      // Для главной страницы - якорная навигация
      const element = document.getElementById(item.id);
      if (element) {
        element.scrollIntoView({ behavior: 'smooth', block: 'start' });
      }
    } else {
      // Для других страниц - обычная навигация
      window.location.href = item.path;
    }
  };

  // Показывать сайдбар только если на главной странице и он видим
  if (!isHomePage || !isVisible) return null;

  return (
    <>
      {/* Десктопная версия - только на главной странице */}
      {isHomePage && (
        <div className="fixed left-6 top-1/2 transform -translate-y-1/2 z-40 hidden lg:block">
          <div className="bg-white/90 backdrop-blur-md rounded-2xl shadow-xl border border-gray-200/60 p-3">
            <div className="space-y-2">
              {navigationItems.map((item) => (
              <button
                key={item.id}
                onClick={() => handleNavigation(item)}
                className={`group flex items-center justify-center w-full p-3 rounded-xl transition-all duration-300 ${
                  activeSection === item.id
                    ? 'bg-blue-50 text-blue-600 shadow-lg border border-blue-200'
                    : 'text-gray-600 hover:text-gray-800 hover:bg-gray-50 hover:shadow-md'
                }`}
                title={item.label}
              >
                  <div className={`w-6 h-6 flex items-center justify-center transition-all duration-300 ${
                    activeSection === item.id ? 'text-blue-600 scale-110' : 'text-gray-500 group-hover:text-gray-700 group-hover:scale-105'
                  }`}>
                    {getNavigationIcon(item.id)}
                  </div>
              </button>
              ))}
            </div>
          </div>
        </div>
      )}

      {/* Мобильная версия - только на главной странице */}
      {isHomePage && (
        <div className="lg:hidden fixed bottom-6 right-6 z-40">
          <div className="bg-white/90 backdrop-blur-md rounded-2xl shadow-2xl border border-gray-200/70 p-2">
            <div className="grid grid-cols-2 gap-2">
              {navigationItems.map((item) => (
                <button
                  key={item.id}
                  onClick={() => handleNavigation(item)}
                  className={`group flex items-center justify-center w-14 h-14 rounded-xl transition-all duration-300 ${
                    activeSection === item.id
                      ? 'bg-blue-50 text-blue-600 shadow-lg border border-blue-200'
                      : 'text-gray-600 hover:text-gray-800 hover:bg-gray-50 hover:shadow-md'
                  }`}
                  title={item.label}
                >
                  <div className={`w-6 h-6 flex items-center justify-center transition-all duration-300 ${
                    activeSection === item.id ? 'text-blue-600 scale-110' : 'text-gray-500 group-hover:text-gray-700 group-hover:scale-105'
                  }`}>
                    {getNavigationIcon(item.id)}
                  </div>
                </button>
              ))}
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default Sidebar;
