      id: 13,
      title: 'Эпидемиологический надзор за инфекционными заболеваниями',
      speaker: 'НЦОЗ МЗ РК',
      category: 'reports',
      date: 'октябрь 2025',
      size: '6.2 MB',
      downloads: 156,
      description: 'Анализ системы эпидемиологического надзора за инфекционными заболеваниями в Казахстане.'
