  const filteredDocuments = activeCategory === 'all'
    ? pdfDocuments
    : pdfDocuments.filter(doc => doc.category === activeCategory);

  const getCategoryName = (categoryId) => {
    return categories.find(cat => cat.id === categoryId)?.name || categoryId;
  };

  const getCategoryColor = (categoryId) => {
    switch (categoryId) {
      case 'presentations': return 'bg-blue-100 text-blue-800';
      case 'materials': return 'bg-green-100 text-green-800';
      case 'guidelines': return 'bg-purple-100 text-purple-800';
      case 'reports': return 'bg-orange-100 text-orange-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const handleDownload = (document) => {
    // Здесь будет логика скачивания файла
    console.log('Downloading:', document.title);
    // Имитация скачивания
    alert(`Скачивание файла: ${document.title}`);
  };

