import React, { useState } from 'react';

const Guidelines = () => {
  const [activeCategory, setActiveCategory] = useState('all');

  const categories = [
    { id: 'all', name: 'Все рекомендации' },
    { id: 'biosafety', name: 'Биологическая безопасность' },
    { id: 'lab', name: 'Лабораторная практика' },
    { id: 'epidemiology', name: 'Эпидемиология' },
    { id: 'hygiene', name: 'Санитария и гигиена' }
  ];

  const guidelines = [
    {
      id: 1,
      category: 'biosafety',
      title: 'Руководство по биологической безопасности в микробиологических лабораториях',
      version: 'Версия 3.0 (2025)',
      description: 'Комплексное руководство по обеспечению биологической безопасности при работе с патогенными микроорганизмами.',
      downloadCount: 1247,
      lastUpdated: 'январь 2025',
      featured: true
    },
    {
      id: 2,
      category: 'lab',
      title: 'Стандартные операционные процедуры для клинико-диагностических лабораторий',
      version: 'Версия 2.1 (2024)',
      description: 'Стандартизированные процедуры для проведения лабораторных исследований в клинической практике.',
      downloadCount: 892,
      lastUpdated: 'декабрь 2024',
      featured: false
    },
    {
      id: 3,
      category: 'epidemiology',
      title: 'Методические рекомендации по эпидемиологическому расследованию вспышек',
      version: 'Версия 1.5 (2025)',
      description: 'Алгоритм действий при выявлении и расследовании вспышек инфекционных заболеваний.',
      downloadCount: 634,
      lastUpdated: 'февраль 2025',
      featured: false
    },
    {
      id: 4,
      category: 'hygiene',
      title: 'Санитарно-эпидемиологические требования к организациям общественного питания',
      version: 'Версия 4.0 (2025)',
      description: 'Актуальные санитарные нормы и требования для организаций общественного питания.',
      downloadCount: 2156,
      lastUpdated: 'март 2025',
      featured: true
    },
    {
      id: 5,
      category: 'biosafety',
      title: 'Требования к средствам индивидуальной защиты в лабораторных условиях',
      version: 'Версия 2.0 (2024)',
      description: 'Рекомендации по выбору и использованию средств индивидуальной защиты.',
      downloadCount: 987,
      lastUpdated: 'ноябрь 2024',
      featured: false
    },
    {
      id: 6,
      category: 'lab',
      title: 'Контроль качества лабораторных исследований',
      version: 'Версия 1.8 (2025)',
      description: 'Система внутреннего и внешнего контроля качества лабораторных исследований.',
      downloadCount: 743,
      lastUpdated: 'январь 2025',
      featured: false
    }
  ];

  const filteredGuidelines = activeCategory === 'all'
    ? guidelines
    : guidelines.filter(item => item.category === activeCategory);

  const getCategoryName = (categoryId) => {
    return categories.find(cat => cat.id === categoryId)?.name || categoryId;
  };

  const getCategoryColor = (categoryId) => {
    switch (categoryId) {
      case 'biosafety': return 'bg-red-100 text-red-800';
      case 'lab': return 'bg-blue-100 text-blue-800';
      case 'epidemiology': return 'bg-green-100 text-green-800';
      case 'hygiene': return 'bg-purple-100 text-purple-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  return (
    <section id="guidelines" className="py-16 bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-12">
          <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
            Методические рекомендации
          </h2>
          <p className="text-lg text-gray-600 max-w-3xl mx-auto">
            Официальные методические рекомендации и руководства по биобезопасности, лабораторной практике и санитарным нормам
          </p>
        </div>

        {/* Фильтры категорий */}
        <div className="flex flex-wrap justify-center gap-3 mb-12">
          {categories.map((category) => (
            <button
              key={category.id}
              onClick={() => setActiveCategory(category.id)}
              className={`px-4 py-2 rounded-lg font-medium text-sm transition-all duration-300 ${
                activeCategory === category.id
                  ? 'bg-blue-600 text-white shadow-lg'
                  : 'bg-white text-gray-700 hover:bg-blue-50 hover:text-blue-600 border border-gray-200'
              }`}
            >
              {category.name}
            </button>
          ))}
        </div>

        {/* Рекомендации */}
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6 max-w-7xl mx-auto">
          {filteredGuidelines.map((guideline) => (
            <div
              key={guideline.id}
              className={`bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden hover:shadow-lg transition-all duration-300 ${
                guideline.featured ? 'ring-2 ring-blue-500' : ''
              }`}
            >
              <div className="p-6">
                <div className="flex items-start justify-between mb-3">
                  <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${getCategoryColor(guideline.category)}`}>
                    {getCategoryName(guideline.category)}
                  </span>
                  {guideline.featured && (
                    <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800">
                      ⭐ Актуально
                    </span>
                  )}
                </div>

                <h3 className="font-bold text-lg text-gray-900 mb-3 leading-tight">
                  {guideline.title}
                </h3>

                <p className="text-gray-600 mb-4 text-sm leading-relaxed">
                  {guideline.description}
                </p>

                <div className="space-y-2 mb-4">
                  <div className="flex justify-between text-sm">
                    <span className="text-gray-500">Версия:</span>
                    <span className="font-medium text-gray-900">{guideline.version}</span>
                  </div>
                  <div className="flex justify-between text-sm">
                    <span className="text-gray-500">Обновлено:</span>
                    <span className="font-medium text-gray-900">{guideline.lastUpdated}</span>
                  </div>
                  <div className="flex justify-between text-sm">
                    <span className="text-gray-500">Скачиваний:</span>
                    <span className="font-medium text-gray-900">{guideline.downloadCount}</span>
                  </div>
                </div>

                <button className="w-full bg-blue-600 hover:bg-blue-700 text-white font-semibold py-3 px-4 rounded-lg transition-colors flex items-center justify-center">
                  <svg className="w-4 h-4 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 10v6m0 0l4-4m-4 4l-4-4m8 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                  </svg>
                  Скачать PDF
                </button>
              </div>
            </div>
          ))}
        </div>

        {/* Архив методических рекомендаций */}
        <div className="mt-16 bg-gradient-to-br from-blue-50 to-cyan-50 rounded-xl p-8 border border-blue-100">
          <div className="text-center mb-8">
            <h3 className="text-2xl font-bold text-gray-900 mb-4">
              Архив методических рекомендаций
            </h3>
            <p className="text-gray-600 max-w-2xl mx-auto">
              Полный архив всех версий методических рекомендаций с возможностью поиска по ключевым словам и темам
            </p>
          </div>

          <div className="grid md:grid-cols-4 gap-6 max-w-4xl mx-auto">
            <div className="text-center">
              <div className="w-16 h-16 flex items-center justify-center mx-auto mb-3">
                <svg className="w-8 h-8 text-blue-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                </svg>
              </div>
              <h4 className="font-semibold text-gray-900 mb-1">Биобезопасность</h4>
              <p className="text-sm text-gray-600">47 документов</p>
            </div>

            <div className="text-center">
              <div className="w-16 h-16 flex items-center justify-center mx-auto mb-3">
                <svg className="w-8 h-8 text-green-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19.428 15.428a2 2 0 00-1.022-.547l-2.387-.477a6 6 0 00-3.86.517l-.318.158a6 6 0 01-3.86.517L6.05 15.21a2 2 0 00-1.806.547M8 4h8l-1 1v5.172a2 2 0 00.586 1.414l5 5c1.26 1.26.367 3.414-1.415 3.414H4.828c-1.782 0-2.674-2.154-1.414-3.414l5-5A2 2 0 009 10.172V5L8 4z" />
                </svg>
              </div>
              <h4 className="font-semibold text-gray-900 mb-1">Лабораторная практика</h4>
              <p className="text-sm text-gray-600">32 документа</p>
            </div>

            <div className="text-center">
              <div className="w-16 h-16 flex items-center justify-center mx-auto mb-3">
                <svg className="w-8 h-8 text-purple-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
                </svg>
              </div>
              <h4 className="font-semibold text-gray-900 mb-1">Эпидемиология</h4>
              <p className="text-sm text-gray-600">28 документов</p>
            </div>

            <div className="text-center">
              <div className="w-16 h-16 bg-orange-100 rounded-full flex items-center justify-center mx-auto mb-3">
                <svg className="w-8 h-8 text-orange-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 10V3L4 14h7v7l9-11h-7z" />
                </svg>
              </div>
              <h4 className="font-semibold text-gray-900 mb-1">Санитария</h4>
              <p className="text-sm text-gray-600">41 документ</p>
            </div>
          </div>

          <div className="text-center mt-8">
            <button className="bg-blue-600 hover:bg-blue-700 text-white font-semibold py-3 px-8 rounded-lg transition-colors">
              Открыть полный архив
            </button>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Guidelines;
