import React, { useState } from 'react';

const MediaGallery = () => {
  const [activeTab, setActiveTab] = useState('photos');
  const [selectedImage, setSelectedImage] = useState(null);

  const photos = [
    {
      id: 1,
      src: '/api/placeholder/600/400',
      alt: 'Открытие семинара - приветственные речи',
      title: 'Церемония открытия',
      description: 'Приветственные речи руководителей организаций',
      category: 'opening'
    },
    {
      id: 2,
      src: '/api/placeholder/600/400',
      alt: 'Доклад Жумабаевой Сулушаш Узаковны',
      title: 'Доклад о биобезопасности',
      description: 'Современные аспекты биологической безопасности',
      category: 'speakers'
    },
    {
      id: 3,
      src: '/api/placeholder/600/400',
      alt: 'Доклад Куатбаевой Айнагул Мухановны',
      title: 'Международные медико-санитарные правила',
      description: 'Анализ международных стандартов в биобезопасности',
      category: 'speakers'
    },
    {
      id: 4,
      src: '/api/placeholder/600/400',
      alt: 'Кофе-брейк между сессиями',
      title: 'Перерыв на кофе-брейк',
      description: 'Неформальное общение участников',
      category: 'breaks'
    },
    {
      id: 5,
      src: '/api/placeholder/600/400',
      alt: 'Демонстрация лабораторного оборудования',
      title: 'Практическая демонстрация',
      description: 'Показ современного лабораторного оборудования',
      category: 'equipment'
    },
    {
      id: 6,
      src: '/api/placeholder/600/400',
      alt: 'Групповое фото участников семинара',
      title: 'Групповое фото',
      description: 'Участники и организаторы семинара',
      category: 'group'
    }
  ];

  const videos = [
    {
      id: 1,
      title: 'Полная запись семинара (День 1)',
      description: 'Видеозапись утренней сессии семинара',
      duration: '4:32:15',
      thumbnail: '/api/placeholder/400/250',
      url: '#'
    },
    {
      id: 2,
      title: 'Доклад Жумабаевой С. У. "Современные аспекты биобезопасности"',
      description: 'Основной доклад о вызовах в области биобезопасности',
      duration: '32:45',
      thumbnail: '/api/placeholder/400/250',
      url: '#'
    },
    {
      id: 3,
      title: 'Доклад Куатбаевой А. М. "Международные медико-санитарные правила"',
      description: 'Анализ международных стандартов биобезопасности',
      duration: '28:12',
      thumbnail: '/api/placeholder/400/250',
      url: '#'
    },
    {
      id: 4,
      title: 'Сессия вопросов и ответов',
      description: 'Интерактивная сессия с участниками',
      duration: '45:33',
      thumbnail: '/api/placeholder/400/250',
      url: '#'
    }
  ];

  const categories = [
    { id: 'all', name: 'Все фото' },
    { id: 'opening', name: 'Открытие' },
    { id: 'speakers', name: 'Доклады' },
    { id: 'breaks', name: 'Перерывы' },
    { id: 'equipment', name: 'Оборудование' },
    { id: 'group', name: 'Групповые' }
  ];

  const filteredPhotos = activeTab === 'photos' ? photos : [];
  const filteredVideos = activeTab === 'videos' ? videos : [];

  return (
    <section id="media" className="py-16 bg-white">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-12">
          <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
            Медиагалерея
          </h2>
          <p className="text-lg text-gray-600 max-w-3xl mx-auto">
            Фото и видео материалы семинара, записи докладов и презентации спикеров
          </p>
        </div>

        {/* Табы для переключения между фото и видео */}
        <div className="flex justify-center mb-12">
          <div className="bg-gray-100 rounded-lg p-1 border border-gray-200">
            <button
              onClick={() => setActiveTab('photos')}
              className={`px-8 py-3 rounded-md font-medium text-base transition-all duration-300 ${
                activeTab === 'photos'
                  ? 'bg-white text-blue-600 shadow-sm border border-blue-200'
                  : 'text-gray-600 hover:text-gray-900 hover:bg-white/50'
              }`}
            >
              📸 Фото ({photos.length})
            </button>
            <button
              onClick={() => setActiveTab('videos')}
              className={`px-8 py-3 rounded-md font-medium text-base transition-all duration-300 ${
                activeTab === 'videos'
                  ? 'bg-white text-blue-600 shadow-sm border border-blue-200'
                  : 'text-gray-600 hover:text-gray-900 hover:bg-white/50'
              }`}
            >
              🎥 Видео ({videos.length})
            </button>
          </div>
        </div>

        {/* Фото галерея */}
        {activeTab === 'photos' && (
          <>
            {/* Фильтры категорий фото */}
            <div className="flex flex-wrap justify-center gap-3 mb-8">
              {categories.map((category) => (
                <button
                  key={category.id}
                  onClick={() => {
                    // Здесь можно добавить фильтрацию по категориям
                    console.log('Фильтр:', category.id);
                  }}
                  className="px-4 py-2 rounded-lg font-medium text-sm bg-gray-100 text-gray-700 hover:bg-gray-200 hover:text-gray-900 transition-colors"
                >
                  {category.name}
                </button>
              ))}
            </div>

            {/* Сетка фото */}
            <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6 mb-12">
              {filteredPhotos.map((photo) => (
                <div
                  key={photo.id}
                  className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden hover:shadow-lg transition-all duration-300 group cursor-pointer"
                  onClick={() => setSelectedImage(photo)}
                >
                  <div className="aspect-[4/3] bg-gradient-to-br from-blue-100 to-cyan-100 relative overflow-hidden">
                    <div className="w-full h-full bg-gray-300 flex items-center justify-center">
                      <svg className="w-12 h-12 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
                      </svg>
                    </div>
                    <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-20 transition-all duration-300 flex items-center justify-center">
                      <div className="opacity-0 group-hover:opacity-100 transition-opacity duration-300">
                        <svg className="w-8 h-8 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                        </svg>
                      </div>
                    </div>
                  </div>

                  <div className="p-4">
                    <h3 className="font-bold text-gray-900 mb-2 text-sm leading-tight">
                      {photo.title}
                    </h3>
                    <p className="text-gray-600 text-xs leading-relaxed">
                      {photo.description}
                    </p>
                  </div>
                </div>
              ))}
            </div>
          </>
        )}

        {/* Видео галерея */}
        {activeTab === 'videos' && (
          <div className="grid md:grid-cols-2 gap-6 mb-12">
            {filteredVideos.map((video) => (
              <div
                key={video.id}
                className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden hover:shadow-lg transition-all duration-300 group cursor-pointer"
              >
                <div className="aspect-video bg-gradient-to-br from-gray-100 to-gray-200 relative overflow-hidden">
                  <div className="w-full h-full bg-gray-300 flex items-center justify-center">
                    <svg className="w-16 h-16 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 10l4.553-2.276A1 1 0 0121 8.618v6.764a1 1 0 01-1.447.894L15 14M5 18h8a2 2 0 002-2V8a2 2 0 00-2-2H5a2 2 0 00-2 2v8a2 2 0 002 2z" />
                    </svg>
                  </div>
                  <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-30 transition-all duration-300 flex items-center justify-center">
                    <div className="opacity-0 group-hover:opacity-100 transition-opacity duration-300">
                      <div className="bg-white rounded-full p-3">
                        <svg className="w-6 h-6 text-gray-800" fill="currentColor" viewBox="0 0 24 24">
                          <path d="M8 5v14l11-7z"/>
                        </svg>
                      </div>
                    </div>
                  </div>
                  <div className="absolute bottom-2 right-2 bg-black bg-opacity-75 text-white px-2 py-1 rounded text-xs font-medium">
                    {video.duration}
                  </div>
                </div>

                <div className="p-4">
                  <h3 className="font-bold text-gray-900 mb-2 leading-tight">
                    {video.title}
                  </h3>
                  <p className="text-gray-600 text-sm leading-relaxed mb-3">
                    {video.description}
                  </p>
                  <button className="w-full bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition-colors text-sm">
                    Смотреть видео
                  </button>
                </div>
              </div>
            ))}
          </div>
        )}

        {/* Модальное окно для просмотра фото */}
        {selectedImage && (
          <div className="fixed inset-0 bg-black bg-opacity-75 z-50 flex items-center justify-center p-4">
            <div className="bg-white rounded-lg max-w-4xl max-h-[90vh] overflow-hidden">
              <div className="flex items-center justify-between p-4 border-b border-gray-200">
                <h3 className="font-bold text-lg text-gray-900">{selectedImage.title}</h3>
                <button
                  onClick={() => setSelectedImage(null)}
                  className="p-2 text-gray-400 hover:text-gray-600 transition-colors"
                >
                  <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  </svg>
                </button>
              </div>
              <div className="p-4">
                <div className="aspect-[4/3] bg-gray-100 rounded-lg flex items-center justify-center mb-4">
                  <svg className="w-24 h-24 text-gray-300" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
                  </svg>
                </div>
                <p className="text-gray-600 text-sm leading-relaxed">
                  {selectedImage.description}
                </p>
              </div>
            </div>
          </div>
        )}

        {/* Архив материалов */}
        <div className="bg-gradient-to-br from-blue-50 to-cyan-50 rounded-xl p-8 border border-blue-100">
          <div className="text-center mb-8">
            <h3 className="text-2xl font-bold text-gray-900 mb-4">
              Архив материалов семинара
            </h3>
            <p className="text-gray-600 max-w-2xl mx-auto">
              Скачайте презентации докладчиков, методические материалы и дополнительные ресурсы
            </p>
          </div>

          <div className="grid md:grid-cols-3 gap-6 max-w-4xl mx-auto">
            <div className="bg-white rounded-lg p-6 border border-gray-200 text-center">
              <div className="w-16 h-16 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <svg className="w-8 h-8 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 4V2a1 1 0 011-1h8a1 1 0 011 1v2M7 4H5a2 2 0 00-2 2v10a2 2 0 002 2h14a2 2 0 002-2V6a2 2 0 00-2-2h-2M7 4h10M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                </svg>
              </div>
              <h4 className="font-bold text-gray-900 mb-2">Презентации докладов</h4>
              <p className="text-sm text-gray-600 mb-4">
                PDF-файлы презентаций всех докладчиков семинара
              </p>
              <button className="w-full bg-red-600 hover:bg-red-700 text-white font-medium py-2 px-4 rounded-lg transition-colors text-sm">
                Скачать архив (PDF)
              </button>
            </div>

            <div className="bg-white rounded-lg p-6 border border-gray-200 text-center">
              <div className="w-16 h-16 bg-blue-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <svg className="w-8 h-8 text-blue-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
                </svg>
              </div>
              <h4 className="font-bold text-gray-900 mb-2">Видеозаписи</h4>
              <p className="text-sm text-gray-600 mb-4">
                Полные записи всех сессий и отдельных докладов
              </p>
              <button className="w-full bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition-colors text-sm">
                Скачать видео
              </button>
            </div>

            <div className="bg-white rounded-lg p-6 border border-gray-200 text-center">
              <div className="w-16 h-16 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <svg className="w-8 h-8 text-green-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-4m-5 0H3m2 0h4M9 7h1m-1 4h1m4-4h1m-1 4h1m-5 10v-5a1 1 0 011-1h2a1 1 0 011 1v5m-4 0h4" />
                </svg>
              </div>
              <h4 className="font-bold text-gray-900 mb-2">Методические материалы</h4>
              <p className="text-sm text-gray-600 mb-4">
                Дополнительные материалы и рекомендации
              </p>
              <button className="w-full bg-green-600 hover:bg-green-700 text-white font-medium py-2 px-4 rounded-lg transition-colors text-sm">
                Скачать материалы
              </button>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default MediaGallery;
